body {
  margin: 3em 9em;
}

header > h1 {
  font: 80px Futura, sans-serif;
  font-weight: 600;
  letter-spacing: 12px;
  text-transform: uppercase;
}

main {
  display: flex;
}

main >*:first-child {
  display: flex;
  flex-direction: column;
}

label[for="grid"] {
  align-self: center;
  padding: 24px 36px;
  margin-top: 48px;
  font: 20px Helvetica, Arial, sans-serif;
  box-shadow: 1px 5px 6px 2px rgba(0, 0, 0, 0.41);
}

main > div {
  flex: 50%;
}

#grid {
  box-shadow: 1px 10px 32px 2px rgba(0, 0, 0, 0.41);
}
